import { Scheme } from './../../models/basic/scheme';
import { Component, OnInit, ViewChild, ElementRef, Input  } from '@angular/core';
import { RestService } from 'src/app/services/rest.service';
import { Search } from 'src/app/models/basic/search';
import { Router } from '@angular/router';



@Component({
  selector: 'app-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.css']
})
export class TableComponent implements OnInit {

  // List of objects
  public stock: any; 

  // Basic
  @Input() public title;
  @Input() public path;
  @Input () public schemes: Scheme[];  
  @Input() public searches : Search[];
 
  // Page
  public currentPage: number;
  public totalItems: number;
  public sortElement: string;
  public sortOrder: string;
  public itemPerPage: number;

  // Links
  public routerLinkA: string;

  // Html DOM 
  public isSearchOpen: boolean = false;

  constructor( private rs: RestService, private router: Router ) { }

  ngOnInit() {
    this.preparation();
    this.loadData();
  }

  preparation(){
    //this.path ="opreme";
    this.sortElement = 'id';
    this.sortOrder = 'asc';
    this.currentPage = 0;
    this.itemPerPage = 12;
    this.routerLinkA = '/podesavanja/' + this.path + '/dodaj';
  }

  loadData(){
    this.rs.getAllP(this.path, this.currentPage, this.itemPerPage, this.sortElement, this.sortOrder, this.searches).subscribe((d)=>{
      this.stock = d.body; this.totalItems =+ d.headers.get('X-Total-Count');
    });
  }

  filter($event){
    this.searches = $event;
    this.currentPage = 0;
    this.loadData();
  }

  changePage($event){
    this.currentPage = $event;
    this.loadData();
  }

  deleteItem(id: number){
    this.rs.delete(this.path, id).subscribe(()=> {this.loadData()});
  }

  changeSort(sortE: string, sortO: string){
    this.sortElement = sortE;
    this.sortOrder = sortO;
    this.currentPage = 0;
    this.currentPage = 0;
    this.loadData();
  }

  checkSort(sortE: string, sortO: string){
    if(this.sortElement == sortE && this.sortOrder == sortO){
      return true;
    }else{
      return false;
    }
  }

  refresh(){
    this.preparation();
    for(let s of this.searches){
      s.value = null;
    }
    if(this.isSearchOpen){
      this.toggleSearch();
    }
   
    this.loadData();
  }

  linkToChange(id: number){
    this.router.navigate(['/podesavanja/' + this.path + '/' + id]);
  }

  objectInObject( s: Scheme){
    if(s.listPath){
      return true;
    }else{
      return false;
    }
  }

  

  

  // Table scroll and hover

  @ViewChild('table', { read: ElementRef }) public table: ElementRef<any>;
  public timer: any;

  // table hover
  trHover: number = -1;

  // table left - right
  public moveToRight(): void {
    this.timer = setInterval(() => {
      this.table.nativeElement.scrollLeft += 30;
    }, 50);
  }

  public moveToLeft(): void {
    this.timer = setInterval(() => {
      this.table.nativeElement.scrollLeft -= 30;
    }, 50);
  }

  public stopMove() {
    clearInterval(this.timer);
  }


  // Toggle search
  toggleSearch(){
    if(this.isSearchOpen){
      setTimeout(() =>{
        this.isSearchOpen = false;
        }, 200
      )
    }else{
      setTimeout(() =>{
        this.isSearchOpen = true;
        }, 200
      )
    }
  }

}
