import { Scheme } from "src/app/models/basic/scheme";
import { FormField } from "./../../models/basic/form-field";
import { Component, OnInit, Input } from "@angular/core";
import {
  FormGroup,
  FormControl,
  FormBuilder,
  Validators
} from "@angular/forms";
import { RestService } from "src/app/services/rest.service";
import { Router } from "@angular/router";
import { ThrowStmt, Attribute } from "@angular/compiler";

@Component({
  selector: "app-form",
  templateUrl: "./form.component.html",
  styleUrls: ["./form.component.css"]
})
export class FormComponent implements OnInit {
  @Input() public title: string = "Oprema";
  @Input() public path: string = "opreme";
  @Input() public schemes: Scheme[];
  @Input() public id: number;

  public stock: any;
  public form: FormGroup;

  // html
  public help: string = "";

  // Links
  public routerLinkA: string;

  constructor(
    private fb: FormBuilder,
    private rs: RestService,
    private router: Router
  ) {}

  ngOnInit() {
    this.routerLinkA = '/podesavanja/' + this.path;
    this.form = this.fb.group({});
    this.createForm();
  }

  createForm() {
    // Create
    for (let s of this.schemes) {
      this.form.addControl(
        s.name,
        new FormControl(s.value, this.addValidators(s))
      );
    }
    // Update
    if (this.id) {
      this.rs.getOne(this.path, this.id).subscribe(d => {
        this.stock = d;
        for (let s of this.schemes) {
          let newObject = {};
          newObject[s.name] = this.stock[s.name];
          this.form.patchValue(newObject);
        }
      });
    }
  }

 

  send() {
    if (this.id) {
      let retVar = this.form.value;
      retVar["id"] = this.id;

      this.rs
        .update(this.path, retVar)
        .subscribe(() => this.router.navigate(["/podesavanja/" + this.path]));
    } else {
      for (let s of this.schemes) {
        if (s.listPath) {
          let newObject = {};
          newObject[s.name] = { id: 2 };
          this.form.patchValue(newObject);
        }
      }
      this.rs
        .save(this.path, this.form.value)
        .subscribe(() => this.router.navigate(["/podesavanja/" + this.path]));
    }
  }

  // Validators
  addValidators(s: FormField) {
    let r: any[] = [];

    if (s.req) {
      r.push(Validators.required);
    }

    if (s.minLength) {
      r.push(Validators.minLength(s.minLength));
    }

    if (s.minValue) {
      r.push(Validators.min(s.minValue));
    }

    if (s.maxValue) {
      r.push(Validators.max(s.maxValue));
    }

    if (s.currency) {
      r.push(Validators.pattern(/[0-9]/));
      //r.push(Validators.pattern(/^-?(0|[0-9]\d*)?$/));
    }

    return r;
  }

  // Inspect error
  test(i: string) {
    let x = this.form.get(i) as FormControl;
    if (x.touched) {
      if (x.invalid) {
        if (x.errors.required) {
          return "Ovo polje je obavezno";
        } else if (x.errors.minlength) {
          return "Unos je prekratak";
        } else if (x.errors.pattern) {
          return "Unos mora biti pozitivan broj";
        } else if (x.errors.min) {
          return "Minimalni unos je " + x.errors.min.min;
        } else if (x.errors.max) {
          return "Maksimalni unos je " + x.errors.max.max;
        } else {
          return "Pogresan unos";
        }
      }
    } else {
      return false;
    }
  }

  changeSelect($event, name: string) {
    let d = $event;
    this.form.patchValue({ [name]: d });
  }
}
