  formatProizvodaNeobrezan(){
    let obrezan = this.get('obrezanFormatProizvodaB');
    let tipProizvoda = this.get('tipProizvoda');
    let sirinaProizvoda  = this.get('sirinaProizvoda');
    let duzinaProizvoda = this.get('duzinaProizvoda');

    if (obrezan){
      if (tipProizvoda === 'Plakat'){
        sirinaProizvoda += 6;
        duzinaProizvoda += 6; 
      }else if (tipProizvoda === 'Letak'){
        sirinaProizvoda += 6;
        duzinaProizvoda += 6; 
      }else if(tipProizvoda === 'Brošura'){
        sirinaProizvoda += 3;
        duzinaProizvoda += 6; 
      }
    }
    this.form.patchValue({ 'sirinaProizvoda': sirinaProizvoda });
    this.form.patchValue({ 'duzinaProizvoda': duzinaProizvoda });
  }

  tabakZaStampu(korice: boolean){
    // Tip proizvoda
    let tipProizvoda = this.get('tipProizvoda');

    // Stamparska masina
    let Wm: number = this.get('masinaZaStampu').x  // maksimalna sirina stamparske masine (mm)
    let Hm: number = this.get('masinaZaStampu').y; // maksimalna duzina stamparkse masine (mm)

    if (korice){
      Wm = this.get('masinaZaStampuKorica').x ;
      Hm = this.get('masinaZaStampuKorica').y;
    }

     // Tehnoloske povrsine
     Wm = Wm - 10;
     Hm = Hm - 19;

     // Dimenzije proizvoda
    let Wp: number = this.get('sirinaProizvoda'); // sirina proizvoda (mm)
    let Hp: number = this.get('duzinaProizvoda'); // duzina proizvoda (mm)

    // Dimenzije tabaka za stampu?

    let sirinaTabakZaStampu: number;
    let duzinaTabakaZaStampu: number;
    let otpadTabakaZaStampu: number;
    let brojProizvodaPoTabakuZaStampu: number;

    if (tipProizvoda === 'Plakat' || tipProizvoda === 'Letak'){

      let Wm_x = Wm;  // Sirina masine
      let Hm_x = Hm; // Duzina masine
      let Wp_x = Wp; // Sirina prozivoda
      let Hp_x = Hp; // Duzina proizvoda
      let WbP_x =  Math.floor(Wm_x / Wp_x); // Br. proizvoda po sirini
      let HbP_x =  Math.floor(Hm_x / Hp_x); // Br. proizvoda po duzini
      let Bp_x = WbP_x * HbP_x // Ukupan broj proizvoda
      let WoP_x = Wm_x - (Wp_x * WbP_x); // Ostatak po sirini
      let HoP_x = Hm_x - (Hp_x * HbP_x); // Ostatak po duzini
      let WoQ_x = false; // Da li ima proizvoda u ostatku
      let HoQ_x = false; // Da li ima proizvoda u ostatku

      if (Hp_x >= Wp_x){
        if (HoP_x >= Wp_x){
          Bp_x = Bp_x + Math.floor(Wm_x / Hp_x);
          WoQ_x = true;
        }
      }else{
        if (WoP_x >= Hp_x){
          Bp_x = Bp_x + Math.floor(Hm_x / Wp_x);
          HoQ_x = true;
        }
      }

      let Op_x = ((Wm_x*Hm_x) - (Bp_x*Wp_x*Hp_x)) / (Wm*Hm) * 100; // Ostatak u procentima

      let Wm_y = Wm; // Sirina masine
      let Hm_y = Hm; // Duzina masine
      let Wp_y = Hp; // Sirina prozivoda
      let Hp_y = Wp; // Duzina proizvoda
      let WbP_y =  Math.floor(Wm_y / Wp_y); // Br. proizvoda po sirini
      let HbP_y =  Math.floor(Hm_y / Hp_y); // Br. proizvoda po duzini
      let Bp_y = WbP_y * HbP_y // Ukupan broj proizvoda
      let WoP_y = Wm_y - (Wp_y * WbP_y); // Ostatak po sirini
      let HoP_y = Hm_y - (Hp_y * HbP_y); // Ostatak po duzini
      let WoQ_y = false; // Da li ima proizvoda u ostatku
      let HoQ_y = false; // Da li ima proizvoda u ostatku

      if (Hp_y >= Wp_y){
        if (HoP_y >= Wp_y){
          Bp_y = Bp_y + Math.floor(Wm_y / Hp_y);
          WoQ_y = true;
        }
      }else{
        if (WoP_y >= Hp_y){
          Bp_y = Bp_y + Math.floor(Hm_y / Wp_y);
          HoQ_y = true;
        }
      }

      let Op_y = ((Wm_y*Hm_y) - (Bp_y*Wp_y*Hp_y)) / (Wm*Hm) * 100;

      let Wst: number;
      let Hst: number;

      if(Bp_x >= Bp_y){
        brojProizvodaPoTabakuZaStampu = Bp_x;
        otpadTabakaZaStampu = Op_x;
        Wst = WbP_x * Wp_x + 10; 
        Hst = HbP_x * Hp_x + 19; 
        if (HoQ_x){
          Wst = Wst + Hp_x;
        }else if(WoQ_x){
          Hst = Hst + Wp_x;
        }
      }else{
        otpadTabakaZaStampu = Op_y;
        brojProizvodaPoTabakuZaStampu = Bp_y;
        Wst = WbP_y * Wp_y + 10; 
        Hst = HbP_y * Hp_y + 19; 

        if (HoQ_y){
          Wst = Wst + Hp_y;
        }else if(WoQ_y){
          Hst = Hst + Wp_y;
        }
      }
      sirinaTabakZaStampu = Wst;
      duzinaTabakaZaStampu = Hst;
    
    }else if (tipProizvoda === 'Brošura'){
      let Wm_x = Wm;  // Sirina masine
      let Hm_x = Hm; // Duzina masine
      let Wp_x = Wp; // Sirina prozivoda
      let Hp_x = Hp; // Duzina proizvoda
      let WbP_x =  Math.floor(Wm_x / Wp_x); // Br. proizvoda po sirini
      let HbP_x =  Math.floor(Hm_x / Hp_x); // Br. proizvoda po duzini
      let Bp_x = WbP_x * HbP_x // Ukupan broj proizvoda

      let Op_x = ((Wm_x*Hm_x) - (Bp_x*Wp_x*Hp_x)) / (Wm*Hm) * 100; // Ostatak u procentima

      let Wm_y = Wm; // Sirina masine
      let Hm_y = Hm; // Duzina masine
      let Wp_y = Hp; // Sirina prozivoda
      let Hp_y = Wp; // Duzina proizvoda
      let WbP_y =  Math.floor(Wm_y / Wp_y); // Br. proizvoda po sirini
      let HbP_y =  Math.floor(Hm_y / Hp_y); // Br. proizvoda po duzini
      let Bp_y = WbP_y * HbP_y // Ukupan broj proizvoda

      let Op_y = ((Wm_y*Hm_y) - (Bp_y*Wp_y*Hp_y)) / (Wm*Hm) * 100;

      let Wst: number;
      let Hst: number;

      if(Bp_x >= Bp_y){
        brojProizvodaPoTabakuZaStampu = Bp_x;
        otpadTabakaZaStampu = Op_x;
        Wst = WbP_x * Wp_x + 10; 
        Hst = HbP_x * Hp_x + 19; 
        
        
      }else{
        otpadTabakaZaStampu = Op_y;
        brojProizvodaPoTabakuZaStampu = Bp_y;
        Wst = WbP_y * Wp_y + 10; 
        Hst = HbP_y * Hp_y + 19; 
        
      }
      sirinaTabakZaStampu = Wst;
      duzinaTabakaZaStampu = Hst;
    }
    
    // Cuvanje
    if (korice){
    this.form.addControl( 'sirinaTabakZaStampuKorice', new FormControl(sirinaTabakZaStampu));
    this.form.addControl( 'duzinaTabakaZaStampuKorice', new FormControl(duzinaTabakaZaStampu));
    this.form.addControl( 'brojProizvodaPoTabakuZaStampuKorice', new FormControl(brojProizvodaPoTabakuZaStampu));
    }else{
      this.form.addControl( 'sirinaTabakZaStampu', new FormControl(sirinaTabakZaStampu));
      this.form.addControl( 'duzinaTabakaZaStampu', new FormControl(duzinaTabakaZaStampu));
      this.form.addControl( 'brojProizvodaPoTabakuZaStampu', new FormControl(brojProizvodaPoTabakuZaStampu));
    }
    //this.form.addControl( 'sirinaTabakZaStampu', new FormControl(sirinaTabakZaStampu));
    //this.form.addControl( 'duzinaTabakaZaStampu', new FormControl(duzinaTabakaZaStampu));
    //this.form.addControl( 'otpadTabakaZaStampu', new FormControl(otpadTabakaZaStampu));
    
  }

