  calculateDigital(){
    // Rezultati
    let res = this.form.value;
    // Broj boja
    let brojBoja: number;
    // Masina za stampu
    let m = res.masinaZaStampu;
    // Tiraz
    let tiraz = res.tiraz;
    // Broj proizvoda na stamparskom tabaku
    let brojProizvodaPoNaTabaku = res.brojProizvodaPoTabakuZaStampu;
    // Cena prvog otiska
    let cenaPrvogOtiska: number;
    // Cena narednog otiska
    let cenaNarednogOtiska: number;
    // Ukupna cena
    let ukupnaCena: number;

    // Broj boja
    if (res.stampaNaObeStrane){
      brojBoja = 8
    }else{
      brojBoja = 4;
    }

    // Tiraz ( u stamp. tabacima)
    tiraz = Math.ceil(tiraz / brojProizvodaPoNaTabaku);

    // Cena prvog otiska
    cenaPrvogOtiska = 
    (8 * m.trosakBoje * m.velicinaMakulature) +
    (m.vremeUlaganja + 0.017) * m.trosakStampe;

    if (!res.stampaNaObeStrane){
      cenaPrvogOtiska = cenaPrvogOtiska / 2
    }

    console.log('Cena prvog otiska: ' +  cenaPrvogOtiska )

    // Broj obrtaja
    let brojObrtaja: number;

    if (m.tip == 'tabacna'){
      brojObrtaja = brojBoja * tiraz;
    }else{
      brojObrtaja = tiraz;
    }
    // Cena narednog otiska
    cenaNarednogOtiska = 
    ((brojObrtaja / m.brzinaStampe) * m.trosakStampe) / tiraz +
    brojBoja * m.trosakBoje;

    console.log('Cena narednog otiska: ' +  cenaNarednogOtiska )

    // Ukupna cena
    ukupnaCena = 
    cenaPrvogOtiska +
    (tiraz - 1) * cenaNarednogOtiska;

    this.ukupnaCena = ukupnaCena;


  }

  // Results
  shareOfsetResults(){
    this.shareOfsetR = true;
    let price = Number(this.ukupnaCena).toFixed(2);
    this.answer =
    price + ' din';
  }

  





