// Specil validators

  checkPrintMachineAndPaper(machine: any) {
    let Wm = machine.x - 10;
    let Hm = machine.y - 19;
    let Wp = this.get('sirinaProizvoda')  + 6;
    let Hp = this.get('duzinaProizvoda')  + 6;
    
    if (Wp > Wm && Wp > Hm){
      return false;
    }
    else if (Hp > Wm && Hp > Hm){
      return false;
    }
    else if (Wp > Wm && Wp < Hm){
      if (Hp > Wm){
        return false;
      }
    }
    else if (Hp < Wm && Hp > Hm){
      if (Wp < Hm){
        return false;
      }
    }


    return true;
  }


