import { Component, OnInit, ViewEncapsulation, ViewChild, ElementRef } from '@angular/core';

import { AuthenticationService } from 'src/app/services-security/authentication-service.service';
import { JwtUtilsService } from 'src/app/services-security/jwt-utils.service';
import { Observable } from 'rxjs';

import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  public user;
  public password: string = '';
  public secretPassword:string = 'X²+2XY+Y²=(X+Y)² '
  public sp = '';
  public passwordI = 0;
  public togglePassword: string = '';
  public eye: boolean = false;

  @ViewChild("input") input: ElementRef;

  

  public wrongUsernameOrPass:boolean;

  constructor(private authenticationService:AuthenticationService,
              private router: Router) {
    this.user = {};
    this.wrongUsernameOrPass = false;
   }

  ngOnInit() {
    
  }

  login(){
    this.authenticationService.login(this.user.username, this.password)
    .subscribe(
      (data) => { 
        let token = data && data['token'];
        let jwtData = token.split('.')[1];
        let decodedJwtJsonData = window.atob(jwtData);
        let decodedJwtData = JSON.parse(decodedJwtJsonData);
        let role = decodedJwtData.role;


        localStorage.setItem('currentUser', JSON.stringify({
          username: 'admin',
          roles: role,
          token: token
        }));
        this.router.navigate(['podesavanja']); 
        return true;
      },
      error => {
        this.wrongUsernameOrPass=true;
        this.password = '';
        this.sp = '';
        this.passwordI = 0;
        this.input.nativeElement.focus();
        return false;
      }
    )
  }

  onChangeValue(value : string ) {  
    if(!value){
      this.password = this.password.slice(0,-1);
      if(this.passwordI == 0){
        this.passwordI = 16;
      }else{
        this.passwordI --;
      }
    }else{
      this.password += value;
      this.sp = this.sp.slice(0, -1);
      this.sp = this.sp + this.secretPassword[this.passwordI];
     
      if(this.passwordI == 16){
        this.passwordI = 0;
      }else{
        this.passwordI ++;
      }

    }
  }

  showPassword(){
    this.togglePassword = this.sp;
    this.sp = this.password;
  }

  hidePassword(){
    this.sp = this.togglePassword;
  }

  focusInput(){
    this.input.nativeElement.focus();
  }


  



}
