import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { JwtUtilsService } from './jwt-utils.service';
import { User } from '../models/basic/user';


@Injectable({
  providedIn: 'root'
})
export class AuthenticationService {

  private readonly loginPath = '/api/login';

  constructor(private http: HttpClient, private jwtUtilsService: JwtUtilsService) { }

  login(username: string, password: string)  {

    var headers: HttpHeaders = new HttpHeaders({ 'Content-Type': 'application/json' });


    return this.http.post(this.loginPath, JSON.stringify({ username, password }), { headers })
    } 
    

  getToken(): String {
    var currentUser = JSON.parse(localStorage.getItem('currentUser'));
    var token = currentUser && currentUser.token;
    return token ? token : "";
  }

  logout(): void {
    localStorage.removeItem('currentUser');
  }

  isLoggedIn(): boolean {
    if (this.getToken() != '') return true;
    else return false;
  }

  getCurrentUser():User {
    if (localStorage.currentUser) {
      return JSON.parse(localStorage.currentUser);
    }
    else {
      return undefined;
    }
  }

  isAdmin(){
     return this.getCurrentUser().roles.indexOf('ADMINISTRATOR')>=0;
  }

}
