import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges, SimpleChange } from '@angular/core';

@Component({
  selector: 'app-pagination',
  templateUrl: './pagination.component.html',
  styleUrls: ['./pagination.component.css']
})
export class PaginationComponent implements OnInit {

  @Input() public totalItems: number;
  @Input() public itemPerPage: number;
  @Output() public eventEmiter = new EventEmitter();
  @Output() public pageEmiter = new EventEmitter<number>(); 
  @Input() public currentPage = 0;
  public totalPages: number;


  constructor() { }

  ngOnInit() {
    this.totalPages = Math.ceil((this.totalItems / this.itemPerPage)) ;
  }

  ngOnChanges( changes: SimpleChanges){
    const totalItems: SimpleChange = changes.totalItems;
    this.totalItems = totalItems.currentValue
    this.totalPages = Math.ceil((this.totalItems / this.itemPerPage)) ;
  }

  public nextPage(){
    if(this.totalPages - 1 > this.currentPage){
      this.currentPage += 1;
      this.pageEmiter.emit(this.currentPage);
    }
  }

  public previous(){
    if(this.currentPage> 0){
      this.currentPage -= 1;
      this.pageEmiter.emit(this.currentPage);
    }
  }

  public firstPage(){
    this.currentPage = 0;
    this.pageEmiter.emit(this.currentPage);
  }

  public lastPage(){
    if(this.totalPages <= 1){
      this.currentPage = 0;
    }else{
      this.currentPage = this.totalPages - 1;
    }
    this.pageEmiter.emit(this.currentPage);
  }
}
