import { Search } from './../../models/basic/search';
import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges, SimpleChange } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from "@angular/forms";
import { trigger, transition, state, style, animate } from '@angular/animations';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css'],
  animations: [
    trigger('animation_1', [
      state('false' , style({ opacity: 0, display: 'none'})),
      state('true', style({ opacity: 1 })),
      transition('1 => 0', animate('200ms')),
      transition('0 => 1', animate('200ms'))
    ])
  ]
  
})
export class SearchComponent implements OnInit {

  public form: FormGroup;

  @Input() public searches;
  @Output() searchesEvent = new EventEmitter<Search[]>();
  @Input() public isSearchOpen;

  // html
  public help: string = '';

  constructor(private fb: FormBuilder) {}

  ngOnInit() {
    this.createForm();
  }

  
  // kreiranje forme
  createForm(){
    this.form = this.fb.group({}); 
    
    for( let s of this.searches){ 
      this.form.addControl(s.name, new FormControl(s.value, this.addValidators(s) ))
    }
  }

  // slanje ispunjene forme
  send(){
    for( let s of this.searches){
      if(this.form.get(s.name)){
        s.value = this.form.get(s.name).value;
      }
    }
    this.searchesEvent.emit(this.searches);
  }

  // dodavanje validatora za polja
  addValidators(s: Search){

    let r: any[] = [];

    if(s.minLength){
      r.push(Validators.minLength(s.minLength));
    }

    if(s.minValue){
      r.push(Validators.min(s.minValue));
    }

    if(s.maxValue){
      r.push(Validators.max(s.maxValue));
    }

    if(s.currency){
      r.push(Validators.pattern(/^-?(0|[1-9]\d*)?$/));
    }

    return r;
  }

  // Provera gresaka 
  test(i: string) {
    let x = this.form.get(i) as FormControl;
    if (x.touched) {
      if (x.invalid) {
        if (x.errors.required) {
          return "Ovo polje je obavezno";
        } else if (x.errors.minlength) {
          return "Unos je prekratak";
        }else if(x.errors.pattern){
          return 'Unos mora biti pozitivan broj'
        } else {
          return "Pogresan unos";
        }
      }
    } else {
      return false;
    }
  }

}
